"use client";
import { useEffect, useState } from "react";
import Header from "../components/Header";
import { PieChart, Pie, Cell, Tooltip, Legend } from "recharts";

export default function DashboardPage() {
  const [tasks, setTasks] = useState([]);
  const token = localStorage.getItem("token");

  const fetchTasks = async () => {
    try {
      const res = await fetch("http://localhost:5000/api/alltasks", {
        headers: { Authorization: `Bearer ${token}` },
      });
      const data = await res.json();
      setTasks(data);
    } catch {
      console.error("Cannot reach backend");
    }
  };

  useEffect(() => {
    if (!token) window.location.href = "/login";
    fetchTasks();
  }, []);

  const completed = tasks.filter(t => !t.pending).length;
  const pending = tasks.filter(t => t.pending).length;

  const chartData = [
    { name: "Completed", value: completed },
    { name: "Pending", value: pending },
  ];
  const COLORS = ["#22c55e", "#f59e0b"];

  return (
    <div className="min-h-screen p-6 bg-sky-200">
      <Header />
      <h2 className="text-3xl font-bold text-center mb-6">Dashboard</h2>
      <div className="flex flex-col md:flex-row gap-8 justify-center items-center">
        <PieChart width={300} height={300}>
          <Pie
            data={chartData}
            dataKey="value"
            nameKey="name"
            cx="50%"
            cy="50%"
            outerRadius={100}
            label
          >
            {chartData.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
          <Tooltip />
          <Legend />
        </PieChart>
        <div className="space-y-2">
          <p>Total Tasks: <b>{tasks.length}</b></p>
          <p>Completed: <b className="text-green-600">{completed}</b></p>
          <p>Pending: <b className="text-amber-600">{pending}</b></p>
          <button
            onClick={() => window.location.href = "/alltasks"}
            className="btn-green mt-2"
          >
            Go to Your Tasks
          </button>
        </div>
      </div>
    </div>
  );
}
